import { App, Services } from "../Core/Global";
import { Format } from "../Core/Helpers/Format";
import { Uri } from "../Core/Helpers/Uri";
import { Signal } from "../Core/Types/Signal";
import { TimeSpan } from "../Core/Types/TimeSpan";
import { Type } from "../Core/Types/Types";
import { IProperties } from "../Templating/Abstraction/IProperties";
import { IPage } from "./Abstraction/IPage";
import { IMultiPageHost, IPageLoadOptions } from "./Abstraction/IPageHost";
import { IView } from "./Abstraction/IView";
import { IViewApplication } from "./Abstraction/IViewApplication";

interface IHistoryState {
    index: number;
    sessionId: string;
}

export class HistoryPageHost implements IMultiPageHost {

    protected _startHistoryIndex: number;
    protected _isSyncing = 0;
    protected _sessionId: string;
    protected _backSignal = new Signal();
    protected _isFirstPage = true;

    constructor(innerHost: IMultiPageHost) {

        this.innerHost = innerHost;

        this._startHistoryIndex = window.history.length;
        this._sessionId = new Date().getTime().toString();

        window.addEventListener("popstate", ev => this.onPopState(ev.state));
    }

    /****************************************/

    protected async onPopState(state: IHistoryState) {

        console.debug("begin popState: ", state, " isSyncing: ", this._isSyncing);
        if (this._isSyncing)
            return;

        if (state)
            await this.goToAsync(state.index);

        this._backSignal.set();

        console.debug("end popState: ", state);
    }

    /****************************************/

    protected onStateChanged(page: IPage) {

        if (this._isSyncing)
            return;

        if (page == this.current)
            this.updateState();
    }

    /****************************************/

    protected updateState(isNew: boolean = false) {

        if (!this.current) {
            window.history.replaceState(window.history.state, "", "/");
            document.title = "";
            return;
        }

        const title = (App as IViewApplication).formatTitle(this.current.title);

        const url = this.current.url ? Uri.absolute(Format.replaceArgs(this.current.url, App.startupArgs)) : null;

        this.registerPageView();

        if (isNew) {

            const curState = <IHistoryState>{
                sessionId: this._sessionId,
                index: this.currentIndex
            };

            window.history.pushState(curState, title, url);
        }
        else
            window.history.replaceState(window.history.state, title, url);

        document.title = title;
    }

    /****************************************/

    protected registerPageView() {

        const url = this.current.url ? Uri.absolute(Format.replaceArgs(this.current.url, App.startupArgs)) : null;

        if ((url != document.location.href || this._isFirstPage) && this.current.url) {
            const relUrl = Format.replaceArgs(this.current.url, App.startupArgs).replace("~/", "");
            if (Services.analytics)
                Services.analytics.pageView(relUrl);
        }
    }

    /****************************************/

    async clearAsync() {

        await this.innerHost.clearAsync();

        await this.syncHistoryAsync();

        this._isSyncing++;

        window.history.pushState(null, null, null);

        window.history.go(-1);

        this._isSyncing--;
    }

    /****************************************/

    get(index: number) : IPage<IView> {
        return this.innerHost.get(index);
    }

    /****************************************/

    async goBackAsync() {

        if (this.currentIndex > 0) {

            console.debug("begin goBack");

            this._backSignal.reset();

            window.history.back();

            const waitRes = await this._backSignal.waitFor(TimeSpan.fromMilliseconds(500));

            console.debug("end goBack: ", waitRes);
        }
        else {
            await this.clearAsync();
            await (App as IViewApplication).mainAsync();
        }

    }

    /****************************************/

    goForwardAsync(): Promise<void> {
        window.history.forward();
        return Promise.resolve();
    }

    /****************************************/

    async goToAsync(index: number) {

        await this.innerHost.goToAsync(index);

        this.updateState();
    }

    /****************************************/

    protected async syncHistoryAsync() {

        if (this._isSyncing)
            return;

        this._isSyncing++;

        try {

            let curState = <IHistoryState>window.history.state;

            while (curState && curState.sessionId == this._sessionId && curState.index > 0) {

                this._backSignal.reset();

                window.history.back();

                await this._backSignal.waitFor(TimeSpan.fromMilliseconds(500));

                curState = <IHistoryState>window.history.state;
            }

            let curIndex = 0;

            while (curIndex < this.pageCount) {

                const curPage = this.get(curIndex);

                const title = (App as IViewApplication).formatTitle(curPage.title);
                const url = curPage.url ? Uri.absolute(Format.replaceArgs(curPage.url, App.startupArgs)) : null;

                curState = {
                    sessionId: this._sessionId,
                    index: curIndex
                };

                if (curIndex == 0)
                    window.history.replaceState(curState, title, url);
                else
                    window.history.pushState(curState, title, url);

                if (curIndex == this.currentIndex)
                    document.title = title;

                curIndex++;
            }

            const delta = this.currentIndex - (this.pageCount - 1);

            if (delta < 0)
                window.history.go(delta);
        }
        finally {
            this._isSyncing--;
        }
    }

    /****************************************/

    async loadAsync<TPage extends IPage>(page: TPage, options?: IPageLoadOptions): Promise<TPage> {

        const curState = <IHistoryState>window.history.state;

        const inSync = curState && curState.sessionId == this._sessionId && curState.index == this.currentIndex;

        await this.innerHost.loadAsync(page, options);

        (page as IProperties & TPage).prop("title").subscribe(a => this.onStateChanged(page));
        (page as IProperties & TPage).prop("url").subscribe(a => this.onStateChanged(page));

        page.host = this;

        if (inSync)
            this.updateState(true);
        else {
            await this.syncHistoryAsync();

            this.registerPageView();
        }

        this._isFirstPage = false;

        return page;
    }

    /****************************************/

    find<TPage extends IPage>(type: Type<TPage>): TPage;

    find<TPage extends IPage>(name: string): TPage;

    find<TPage extends IPage>(nameOrType: string | Type<TPage>): TPage {
        return this.innerHost.find(nameOrType as Type<TPage>);
    }

    /****************************************/

    async bringFrontAsync(page: IPage) {

        await this.innerHost.bringFrontAsync(page);

        await this.syncHistoryAsync();
    }

    /****************************************/

    get currentIndex(): number {
        return this.innerHost.currentIndex;
    }

    set setCurrentIndex(value: number) {
        this.innerHost.currentIndex = value;
        this.syncHistoryAsync();
    }

    /****************************************/

    get current(): IPage {
        return this.innerHost.current;
    }

    /****************************************/

    get pageCount(): number {
        return this.innerHost.pageCount;
    }

    /****************************************/

    get canGoBack(): boolean {
        return this.innerHost.canGoBack || (App as IViewApplication).hasMain;
    }

    /****************************************/

    innerHost: IMultiPageHost;

}