import { IDictionary } from "../../Core/Abstraction/IDictionary";


declare global {

    export interface IServices {

        itemsObserver: ItemsObserver;
    }
}


interface IItemChangeArgs<TItem, TValue> {
    itemType: string;
    value?: TValue;
    item?: TItem;
}

/****************************************/

export interface IItemChangeListener<TItem, TValue> {

    onItemAdded?(args: IItemChangeArgs<TItem, TValue>): void;

    onItemRemoved?(args: IItemChangeArgs<TItem, TValue>): void;

    onItemChanged?(args: IItemChangeArgs<TItem, TValue>): void;
}

/****************************************/

export class ItemsObserver {

    private _listener: IDictionary<IItemChangeListener<any, any>[]> = {}

    constructor() {
    }

    /****************************************/

    unregister<TItem, TValue>(itemType: string, listener: IItemChangeListener<TItem, TValue>) {

        if (itemType in this._listener) {
            const listeners = this._listener[itemType];
            const index = listeners.indexOf(listener);
            if (index! - 1)
                listeners.splice(index, 1);
        }
    }

    /****************************************/

    register<TItem, TValue>(itemType: string, listener: IItemChangeListener<TItem, TValue>) {

        if (!(itemType in this._listener))
            this._listener[itemType] = [listener];
        else {

            if (this._listener[itemType].indexOf(listener) == -1)
                this._listener[itemType].push(listener);
        }
    }

    /****************************************/

    clear() {
        this._listener = {}
    }

    /****************************************/

    notifyAdded<TItem, TValue>(args: IItemChangeArgs<TItem, TValue>) {

        if (args.itemType in this._listener)
            this._listener[args.itemType].forEach(a => a.onItemAdded ? a.onItemAdded(args) : undefined);
    }

    /****************************************/

    notifyRemoved<TItem, TValue>(args: IItemChangeArgs<TItem, TValue>) {

        if (args.itemType in this._listener)
            this._listener[args.itemType].forEach(a => a.onItemRemoved ? a.onItemRemoved(args) : undefined);
    }

    /****************************************/

    notifyChanged<TItem, TValue>(args: IItemChangeArgs<TItem, TValue>) {

        if (args.itemType in this._listener)
            this._listener[args.itemType].forEach(a => a.onItemChanged ? a.onItemChanged(args) : undefined);
    }
}