
export function apply<T>(item: T, block: (T) => void): T {
    block(item);
    return item;
}

/****************************************/

export function injectProxy<T>(proxy: {}, propName: string, value: T): T {
    proxy[propName] = value;
    return <T>proxy[propName];
}

/****************************************/

export function safeCall<T>(block: () => T, errorHandler?: (e: string) => T): T {
    try {
        return block();
    }
    catch (e) {
        if (errorHandler)
            return errorHandler(e);
    }
}

/****************************************/


export async function safeCallAsync<T>(block: () => Promise<T>, errorHandler?: (e: string) => T): Promise<T> {
    try {
        return await block();
    }
    catch (e) {
        if (errorHandler)
            return Promise.resolve(errorHandler(e));
    }
}