import { Format } from "../../Core/Helpers/Format";
import { LocalString } from "../../Core/Types/DynamicString";
import { BindableObject } from "../../Templating/BindableObject";
import { IView, ViewStyle } from "../Abstraction/IView";
import { IViewContent } from "../Abstraction/IViewContent";
import { IViewContentHost } from "../Abstraction/IViewContentHost";
import { IViewContentInfo } from "../Abstraction/IViewContentInfo";
import { IViewContentProvider } from "../Abstraction/IViewContentProvider";
import { ContentActivity } from "../Activities/ContentActivity";
import { ContentHostView } from "../Components/ContentHostView";
import { IconTextView } from "../Components/IconTextView";
import { Actions } from "../Helpers/Actions";

type MasterDetailsMode = "auto" | "separate" | "split-h";


export interface IMasterContent<TItem, TDetails extends IViewContentProvider> extends IViewContentProvider {

    getDetailsAsync(item: TItem): Promise<TDetails>;

    masterHost: MasterDetailsContent<TItem, this, TDetails>;
}

/****************************************/

export interface IMasterDetailsViewConfig<TItem, TMaster extends IMasterContent<TItem, TDetails>, TDetails extends IViewContentProvider> {

    itemDisplayName?: LocalString;

    masterContent: TMaster;

    mode?: MasterDetailsMode;

    styles?: ViewStyle[];

    minWidth?: number;
}

/****************************************/

export class MasterDetailsContent<TItem, TMaster extends IMasterContent<TItem, TDetails> = any, TDetails extends IViewContentProvider = any> extends BindableObject implements IViewContentProvider {

    protected _host: IViewContentHost;

    protected _masterContainer: ContentHostView<TMaster>;

    protected _detailsContainer: ContentHostView<TDetails>;

    constructor(config?: IMasterDetailsViewConfig<TItem, TMaster, TDetails>) {

        super();

        this.bindConfig("mode", config);

        this.bindConfig("masterContent", config);

        this.bindConfig("itemDisplayName", config);

        this.bindConfig("minWidth", config);

        if (config != null) {

            if (config.styles)
                config.styles.forEach(a => this.styles.push(a));
        }

        if (this.masterContent)
            this.masterContent.masterHost = this;
    }

    /****************************************/

    activateAsync(reason?) {

        if (this.masterContent.activateAsync)
            return this.masterContent.activateAsync(reason);
        return Promise.resolve();
    }

    /****************************************/

    deactivateAsync() {

        if (this.masterContent.deactivateAsync)
            return this.masterContent.deactivateAsync();
        return Promise.resolve();

    }

    /****************************************/

    async getContentAsync(host: IViewContentHost) {

        this._host = host;


        if (this.mode == "auto") {
            const curWidth = document.body.clientWidth / window.devicePixelRatio;
            if (curWidth > this.minWidth)
                this.mode = "split-h";
            else
                this.mode = "separate";
        }

        if (this.mode != "separate") {

            const views: IView[] = [];

            this._masterContainer = new ContentHostView({
                name: "master",
                contentProvider: this.masterContent
            });

            this._detailsContainer = new ContentHostView<TDetails>({
                name: "details",
                emptyView: new IconTextView({
                    styles: ["empty-view"],
                    icon: this.info.icon,
                    text: Format.message("msg-select-an-item", this.itemDisplayName)
                })
            });

            views.push(this._masterContainer);

            views.push(this._detailsContainer);

            return Promise.resolve({
                views: views,
                styles: [...this.styles, this.mode],
                title: this.info.displayName
            } as IViewContent);
        }
        else
            return this.masterContent.getContentAsync(host);

    }

    /****************************************/

    async showDetailsAsync(item: TItem) {

        if (this.isDetailsLoading)
            return;

        try {

            this.isDetailsLoading = true;

            if (!item) {

                if (this.mode != "separate")
                    await this._detailsContainer.closeAsync();
            }
            else {

                const details = await this.masterContent.getDetailsAsync(item);

                if (!details)
                    return;

                if (this.mode != "separate") {

                    if (this._detailsContainer.contentProvider?.deactivateAsync)
                        await this._detailsContainer.contentProvider.deactivateAsync();

                    this._detailsContainer.contentProvider = details;

                    await this._detailsContainer.loadAsync();
                }
                else {

                    await Actions.loadPageAsync(new ContentActivity({
                        providers: [details],
                        name: details.info.name,
                    }));

                }
                this.details = details;
            }
        }
        finally {
            this.isDetailsLoading = false;
        }
    }

    /****************************************/

    isDetailsLoading: boolean = false;

    details: TDetails = null;

    minWidth: number = 400;

    masterContent: TMaster = null;

    styles: ViewStyle[] = ["horizontal", "fill-v", "fill-h", "fill-items-v", "master-details"];

    mode: MasterDetailsMode = "auto";

    itemDisplayName: LocalString = null;

    get info(): IViewContentInfo { return this.masterContent.info }
}