import { linq } from "../Core/Linq";

export interface IMatchResult<T> {
    rank: number;
    value: T;
}

export interface ISearchPart { 
    weight: number; 
    text: string;
}
 
/****************************************/

export abstract class BaseSearch<TValue> {

    searchAsync(query: string, chunkSize: number = 25): Promise<TValue[]> {

        if (!query)
            query = "";
        const queryParts = linq(query.toLowerCase().split(" ")).select(a => a.trim()).where(a => a != "").toArray();

        const result: IMatchResult<TValue>[] = [];

        if (queryParts.length > 0)
            this.doSearch(queryParts, result);

        return linq(result).orderByDesc(a => a.rank).select(a => a.value).toArrayAsync(chunkSize);
    }

    /****************************************/

    protected macthWeight(keyword: string, text: string) {

        if (!text)
            return 0;

        if (typeof text != "string")
            text = (text as object).toString();

        text = text.toLowerCase();

        const index = text.indexOf(keyword);

        if (index == -1)
            return 0;

        return (text.length == keyword.length ? 1 : (text.length - keyword.length - index) / (text.length - keyword.length)) +
            (text.length - keyword.length) / text.length
    }

    /****************************************/

    protected abstract getSearchParts(value: TValue): Iterable<ISearchPart>;

    protected abstract doSearch(queryParts: string[], result: IMatchResult<TValue>[]);

    /****************************************/

    protected matchValue(value: TValue, queryParts: string[]): number {

        if (queryParts.length == 0)
            return 1;

        let totWeight: number = 0;

        for (const qPart of queryParts) {

            let partWeight = 0;

            for (const valuePart of this.getSearchParts(value))
                partWeight += this.macthWeight(qPart, valuePart.text) * valuePart.weight ?? 1;

            if (partWeight == 0)
                return 0;

            totWeight += partWeight;
        }

        return totWeight;
    }
}