import { IRepository } from "../../Core/Abstraction/IRepository";
import { Format } from "../../Core/Helpers/Format";
import { linq } from "../../Core/Linq";
import { IAction } from "../Abstraction/IAction";
import { ISimpleItem } from "../Abstraction/ISimpleItem";
import { IItemsSourceConfig, ItemsSource } from "../Items/ItemsSource";


export function repositoryItems<TItem, TValue, TFilter>(repo: IRepository<TItem, TValue, TFilter>, config?: IItemsSourceConfig<TItem, TValue, TFilter>) {

    return new ItemsSource<TItem, TValue, TFilter>({
        getItemsAsync: async filter => Array.from(await repo.listAsync(filter)),
        getItemByValueAsync: value => repo.getByIdAsync(value),
        ...config
    });
}

/****************************************/

export function staticItems<TValue>(items: ISimpleItem<TValue>[], allowZero: boolean = false) {

    return new ItemsSource<ISimpleItem<TValue>, TValue>({
        typeName: "WebApp.ISimpleItem",
        getItemValue: a => a.value,
        getItemText: a => Format.title(a.text),
        getItemsAsync: () => Promise.resolve(items),
        getItemByValueAsync: value => Promise.resolve(linq(items).first(a => a.value == value)),
        allowZero: allowZero
    });
}

/****************************************/

export function arrayItems<TItem>(items: TItem[]) {

    return new ItemsSource<TItem, TItem>({
        getItemsAsync: () => Promise.resolve(items)
    });
}

/****************************************/

export function actionItems(items: IAction[]) {

    return new ItemsSource<IAction, IAction>({
        typeName: "WebApp.IAction",
        getItemText: a => Format.text(a.displayName),
        getItemsAsync: () => Promise.resolve(items),
        allowZero: false
    });
}