import { IRepository } from "../Abstraction/IRepository";

interface ILocalDbRepositoryConfig<TObj, TKey extends IDBValidKey> {

    dbName: string;

    tableName: string;

    getObjectId?: (obj: TObj) => TKey; 
}

/****************************************/

export class LocalDbRrepositoy<TObj, TKey extends IDBValidKey, TFilter> implements IRepository<TObj, TKey, TFilter> {

    protected _config: ILocalDbRepositoryConfig<TObj, TKey>
    protected _database: IDBDatabase;

    constructor(config: ILocalDbRepositoryConfig<TObj, TKey>) {
        this._config = config;

        if (config.getObjectId)
            this.getObjectId = config.getObjectId;
    }

    /****************************************/

    protected getObjectId(obj: TObj): TKey {

        return obj["id"] as TKey;
    }

    /****************************************/

    protected openOrCreateAsync() {

        if (this._database)
            return;

        return new Promise((res, rej) => {

            const request = indexedDB.open(this._config.dbName, 2);

            request.onerror = ev => rej();

            request.onsuccess = ev => {

                this._database = request.result;
                res(true);
            }

            request.onupgradeneeded = ev => {

                if (!request.result.objectStoreNames.contains(this._config.tableName))
                    request.result.createObjectStore(this._config.tableName);
                //this._database = request.result;
                //res(true);
            }
        });
    }

    /****************************************/

    protected async beginTransactionAsync(readwrite = true) {
        await this.openOrCreateAsync();
        return this._database.transaction(this._config.tableName, readwrite ? "readwrite" : "readonly").objectStore(this._config.tableName);
    }

    /****************************************/

    protected async requestAsync<T>(action: (store: IDBObjectStore) => IDBRequest<T>, readwrite = true): Promise<T> {

        const store = await this.beginTransactionAsync(readwrite);

        return new Promise((res, rej) => {
            const request = action(store);
            request.onerror = ev => rej();
            request.onsuccess = ev => res(request.result);
        });
    }

    /****************************************/

    async listAsync(filter?: TFilter): Promise<Iterable<TObj>> {

        const items = await this.requestAsync(store => store.getAll());
        return items as TObj[];
    }

    /****************************************/

    async getByIdAsync(id: TKey): Promise<TObj> {

        const item = await this.requestAsync(store => store.get(id));
        return item as TObj;
    }

    /****************************************/

    async deleteByIdAsync(id: TKey): Promise<boolean> {
        await this.requestAsync(store => store.delete(id));
        return true;
    }

    /****************************************/

    async addAsync(obj: TObj): Promise<TKey> {

        const key = await this.requestAsync(store => store.add(obj, this.getObjectId(obj)));
        return key as TKey;
    }

    /****************************************/

    async updateAsync(obj: Partial<TObj>, id: TKey): Promise<boolean> {

        const curItem = await this.getByIdAsync(id);
        Object.assign(curItem, obj);
        await this.requestAsync(store => store.put(obj, id));
        return true;
    }
}