import { IErrorHandler } from "../Core/Abstraction/IErrorHandler";;
import { DbStorage } from "../Core/Services/DbStorage";
import { LocalStorageCache } from "../Core/Services/LocalStorageCache";
import { XHRHttpClient } from "../Core/Services/XHRHttpClient";
import { StringTable } from "../Core/StringTable";
import { behavoirCatalog } from "../Templating/Abstraction/IBehavoir";
import { BindableObject } from "../Templating/BindableObject";
import { template } from "../Templating/Functions";
import { IPage } from "./Abstraction/IPage";
import { IMultiPageHost } from "./Abstraction/IPageHost";
import { IView } from "./Abstraction/IView";
import { IViewApplication } from "./Abstraction/IViewApplication";
import { AttachBehavoir } from "./Behavoirs/AttachBehavoir";
import { LongPressBehavoir } from "./Behavoirs/LongPresssBehavoir";
import { RippleClickBehavoir } from "./Behavoirs/RippleClickBehavoir";
import { ScrollCheckBehavoir } from "./Behavoirs/ScrollCheckBehavoir";
import { VibrateClickBehavoir } from "./Behavoirs/VibrateClickBehavoir";
import { Blocker } from "./Components/Blocker";
import { SelfHostedPageHost } from "./Components/SelfHostedPageHost";
import { SlidePageHost } from "./Components/SlidePageHost";
import { HistoryPageHost } from "./HIstoryPageHost";
import { ItemsObserver } from "./Items/ItemsObserver";
import { ViewOperationManager } from "./Operation";
import { DynamicPageManager } from "./Services/DynamicPageManager";
import "./AllTemplates"
import { Services } from "../Core/Global";
import { GesturekBehavoir } from "./Behavoirs/GestureBehavoir";
import { IAppArgs } from "../Core/Abstraction/IApplication";
import { GoogleAnalytics } from "../Core/Services/GoogleAnalytics";
import { ContextMenuBehavoir } from "./Behavoirs/ContextMenuBehavoir";


export class ViewApplication<TArgs extends IAppArgs = IAppArgs> extends BindableObject implements IViewApplication<TArgs>, IErrorHandler {

    protected _blockCount = 0;
    protected _blocker: Blocker;
    protected _startupArgs: TArgs;

    constructor() {
        super();
        this.prop("language").subscribe(() => this.updateLanguage());
    }

    /****************************************/

    async runAsync(args?: TArgs) {

        this.language = (window.navigator.language).split("-")[0].toUpperCase();

        window.addEventListener("error", ev => this.handleError(ev.srcElement, ev.error, ev.message));

        this._startupArgs = { baseUrl: "", ...args};

        if (this.baseUrl.length == 0 || this.baseUrl[this.baseUrl.length - 1] != "/")
            this.baseUrl += "/";

        this.initServices();

        await this.configureServicesAsync();

        if (this.isSelfHosted) {
            this.pageHost = new SelfHostedPageHost();
            this.view = <any>this.pageHost;
            //window["WebApp"]["Actions"] = window.parent["WebApp"]["Actions"];
        }
        else {
            this.pageHost = new HistoryPageHost(this.createPageHost());
            this.view = (this.pageHost as HistoryPageHost).innerHost as unknown as IView;
        }

        Services.pageHost = this.pageHost;

        document.title = this.appName;

        template(document.body, this.view.template, this.view);

        this.onStarted();
    }

    /****************************************/

    protected updateLanguage() {

        document.documentElement.lang = this.language;

        StringTable.currentConfig = {
            language: this.language
        };
    }

    /****************************************/

    protected onStarted() {

    }

    /****************************************/

    handleError(source: any, error: any, message?: string) {

        console.error("Source: ", source, "Error: ", error, "Message: ", message);
    }

    /****************************************/

    formatTitle(title: string): string {
        if (this.appName && title)
            return this.appName + " - " + title;
        if (title)
            return title;
        return this.appName;
    }

    /****************************************/

    protected createPageHost(): IMultiPageHost {
        return new SlidePageHost();
    }

    /****************************************/

    protected initServices() {

        Services.application = this;
        Services.pageManager = new DynamicPageManager();
        Services.itemsObserver = new ItemsObserver();

        Services.httpClient = new XHRHttpClient();
        Services.cache = new LocalStorageCache();
        Services.dbStorage = new DbStorage();
        Services.operation = new ViewOperationManager();

        if (this.startupArgs.gaId)
            Services.analytics = new GoogleAnalytics(this.startupArgs.gaId);

        behavoirCatalog["attach"] = () => AttachBehavoir.instance;
        behavoirCatalog["ripple"] = () => RippleClickBehavoir.instance;
        behavoirCatalog["vibrate"] = () => VibrateClickBehavoir.instance;
        behavoirCatalog["scroll-check"] = () => ScrollCheckBehavoir.instance;
        behavoirCatalog["long-press"] = () => new LongPressBehavoir();
        behavoirCatalog["gesture"] = () => new GesturekBehavoir();
        behavoirCatalog["context-menu"] = () => ContextMenuBehavoir.instance;
    }

    /****************************************/

    protected configureServicesAsync(): Promise<any> {

        return Promise.resolve();
    }

    /****************************************/

    block(content?: IView): void {

        this._blockCount++;

        if (!this._blocker) {

            this._blocker = new Blocker();

            setTimeout(() => this.restoreBlock(content));
        }
        else
            this.restoreBlock(content);
    }

    /****************************************/

    restoreBlock(content?: IView) {

        if (this._blockCount > 0 && this._blocker.status != "show" && this._blocker.status != "showing")
            this._blocker.showAsync(content);
        else if (content)
            this._blocker.content = content;
    }

    /****************************************/

    unblock(force: boolean = false) {

        if (!this._blocker)
            return;

        if (force) {

            if (this._blocker.status == "show" || this._blocker.status == "showing")
                this._blocker.hideAsync();
        }
        else {
            if (--this._blockCount <= 0)
                this._blocker.hideAsync();
        }
    }

    /****************************************/

    mainAsync() {

        return Promise.resolve<IPage>(null);
    }

    /****************************************/

    get baseUrl(): string {
        return this._startupArgs.baseUrl;
    }

    get isDev() {
        return this._startupArgs["env"] == "Development";
    }

    set baseUrl(value: string) {
        this._startupArgs.baseUrl = value;
    }

    get startupArgs() {
        return this._startupArgs;
    }

    get isSelfHosted(): boolean {
        try {
            return window.parent != window && window.parent["WebApp"] && window.parent["WebApp"]["App"];
        }
        catch (e) {
            return false;
        }
    }

    get hasMain(): boolean {
        return false;
    }

    pageHost: IMultiPageHost = null;

    appName: string = null;

    view: IView = null;

    language: string = null;
}