import { ICacheEntry, ICacheManager } from "../Abstraction/ICacheManager";
import { now } from "../Utils/DateUtils";

export class LocalStorageCache implements ICacheManager {

    read<T>(key: string): ICacheEntry<T> {

        const entryJson = localStorage.getItem("cache:" + key);

        if (entryJson) {
            const entry = <ICacheEntry<T>>JSON.parse(entryJson);
            entry.readCount++;
            entry.lastReadTime = now(); 
            localStorage.setItem("cache:" + key, JSON.stringify(entry));
            return entry;
        }

        return null;
    }

    /****************************************/

    update<T>(key: string, value: T): ICacheEntry<T> {

        let entry = this.read<T>(key);

        if (!entry) {
            entry = {
                creationTime: now(),
                key: key,
                lastReadTime: null,
                lastWriteTime: null,
                value: value,
                readCount: 0,
                writeCount: 0
            };
        }
        else {
            entry.writeCount++;
            entry.lastWriteTime = now();
            entry.value = value;
        }

        localStorage.setItem("cache:" + key, JSON.stringify(entry));

        return entry;
    }

    /****************************************/

    remove(key: string) {
        localStorage.removeItem("cache:" + key);
    }

    /****************************************/

    clear() {
        for (let i = localStorage.length - 1; i >= 0; i--) {
            const key = localStorage.key(i);
            if (key.startsWith("cache:"))
                localStorage.removeItem(key);
        }
    }

    /****************************************/

    contains(key: string): boolean {

        return localStorage.getItem("cache:" + key) != null;
    }

}