import { Services } from "../../Core/Global";
import { linq } from "../../Core/Linq";
import { Signal } from "../../Core/Types/Signal";
import { EqualityComparer } from "../../Core/Types/Types";
import { EditableItemsSource } from "./EditableItemsSource";

export interface IValueCache<T> {

    value?: T;
    lastUpdateTime?: Date;
    updateComplete?: Signal;
    key: string;
}

/****************************************/


export interface ICachedEditableItemsSourceConfig<TItem, TValue, TFilter, TUpdate, TAdd> {

    source: EditableItemsSource<TItem, TValue, TFilter, TUpdate, TAdd>;

    filterItem?: (filter: TFilter, item: TItem, items: Map<TValue, TItem>) => boolean;

    filter?: TFilter;

    finalize?: (filter: TFilter, items: TItem[]) => void;

    cache?: IValueCache<Map<TValue, TItem>>;
}

/****************************************/

export class CachedEditableItemsSource<TItem, TValue, TFilter, TUpdate, TAdd> extends EditableItemsSource<TItem, TValue, TFilter, TUpdate, TAdd> {

    constructor(config?: ICachedEditableItemsSourceConfig<TItem, TValue, TFilter, TUpdate, TAdd>) {
        super();

        if (config) {

            this.source = config.source;

            this.cache = config.cache;

            if (config.filterItem)
                this.filterItem = (filter, item) => config.filterItem(filter, item, this.items);

            if (config.finalize)
                this.finalize = config.finalize;
        }

        if (!this.cache)
            this.cache = {
                key: new Date().getTime().toString(),
                lastUpdateTime: null,
                value: null
            };

        if (!this.cache.updateComplete)
            this.cache.updateComplete = new Signal(true);

        if (!this.cache.value)
            this.cache.value = new Map<TValue, TItem>();

        else if (this.cache.value.size > 0)
            this.cache.lastUpdateTime = new Date();
    }

    /****************************************/

    getItemText(item) {

        return this.source.getItemText(item);
    }

    /****************************************/

    getItemValue(item, bindItem) {

        return this.source.getItemValue(item, bindItem);
    }

    /****************************************/

    getFilter(text, offset, pageSize) {

        return this.source.getFilter(text, offset, pageSize);
    }

    /****************************************/

    equals(a, b) {

        return this.source.equals(a, b);
    }


    /********************************************/

    newItem() {
        return this.source.newItem();
    }

    /****************************************/

    finalize(filter: TFilter, items: TItem[]) {

    }

    /****************************************/

    async countAsync(filter: TFilter): Promise<number> {

        return linq(this.items.entries()).select(a => a[1]).where(a => this.filterItem(filter, a)).count();
    }

    /****************************************/

    async getItemsAsync(filter) {

        await this.updateCacheAsync(false);

        const items = await linq(this.items.entries()).select(a => a[1]).where(a => this.filterItem(filter, a)).toArrayAsync(10);
        this.finalize(filter, items);
        return items;
    }

    /****************************************/

    async getItemByValueAsync(value) {

        await this.updateCacheAsync(false);

        if (value == null)
            return null;

        if (typeof value == "object" && typeof value["valueOf"] == "function")
            value = value["valueOf"]();

        return Promise.resolve(this.items.get(value));
    }

    /********************************************/

    async addItemAsync(item) {

        const newItem = await this.source.addItemAsync(item);
        if (newItem != null)
            this.items.set(this.getItemValue(newItem, false), newItem);
        return newItem;
    }

    /********************************************/

    async updateItemAsync(item) {
        const newItem = await this.source.updateItemAsync(item);

        if (newItem != null)
            this.items.set(this.getItemValue(newItem, false), newItem);

        return newItem;
    }

    /********************************************/

    async removeItemAsync(item) {
        const result = await this.source.removeItemAsync(item);
        if (result)
            this.items.delete(this.getItemValue(item, false));
        return result;
    }

    /****************************************/

    protected filterItem(filter: TFilter, item: TItem) {

        return true;
    }

    /****************************************/

    protected async readCacheAsync(): Promise<TItem[]> {

        if (this.cache.key)
            return await Services.dbStorage.getItem<TItem[]>(this.cache.key);
    }

    /****************************************/

    protected async writeCacheAsync(items: TItem[]) {

        if (this.cache.key)
            await Services.dbStorage.setItem(this.cache.key, items);
    }

    /****************************************/

    async updateCacheAsync(force: boolean) {

        if (this.cache.lastUpdateTime && !force)
            return;

        if (!this.cache.updateComplete.isSet) {
            await this.cache.updateComplete.waitFor();
            return;
        }

        this.cache.updateComplete.reset();

        try {
            let newItems: TItem[];

            try {

                newItems = await this.source.getItemsAsync(this.filter);
            }
            catch (ex) {

                console.error(ex);
            }

            let mustUpdate = true;

            if (newItems == null) {
                newItems = await this.readCacheAsync();
                mustUpdate = false;
            }

            if (newItems != null) {

                this.items.clear();

                for (const item of newItems)
                    this.items.set(this.getItemValue(item, false), item);

                this.cache.lastUpdateTime = new Date();

                if (mustUpdate)
                    await this.writeCacheAsync(newItems);
            }
        }
        finally {

            this.cache.updateComplete.set();
        }
    }

    /****************************************/

    get typeName() {
        return this.source.typeName;
    }

    get allowZero() {
        return this.source.allowZero;
    }

    get itemComparer(): EqualityComparer<TItem> {

        return this.source.itemComparer;
    }

    get items() {
        return this.cache.value;
    }

    /****************************************/

    source: EditableItemsSource<TItem, TValue, TFilter, TUpdate, TAdd> = null;

    cache: IValueCache<Map<TValue, TItem>> = null;

    filter: TFilter = null;
}