import { Services } from "../../Core/Global";
import { IMapManager } from "../Abstraction/IMapManager";
import { IRefreshable } from "../Abstraction/IRefreshable";
import { IViewContent } from "../Abstraction/IViewContent";
import { IViewContentHost } from "../Abstraction/IViewContentHost";
import { IViewContentInfo } from "../Abstraction/IViewContentInfo";
import { IViewContentProvider } from "../Abstraction/IViewContentProvider";
import { MapView } from "../Components/MapView";
import { ProgressView } from "../Components/ProgressView";


export interface IMapContentConfig {

    showCurrentPosition?: boolean;
}

/****************************************/

export abstract class MapContent implements IViewContentProvider, IRefreshable {

    protected _mapView: MapView;
    protected _progress: ProgressView;
    protected _showCurrentPosition: boolean;

    /****************************************/

    constructor(config?: IMapContentConfig) {
        if (config) {
            if (config.showCurrentPosition !== undefined)
                this._showCurrentPosition = config.showCurrentPosition;
        }
    }

    /****************************************/

    refreshAsync(): Promise<void> {
        return null;
    }

    /****************************************/

    async getContentAsync(host: IViewContentHost): Promise<IViewContent> {

        this._mapView = new MapView({
            onMapLoaded: () => this.onMapLoaded(),
            manager: Services.mapManager(),
            showCurrentPosition: this._showCurrentPosition
        });

        this._progress = new ProgressView();

        return <IViewContent>{
            views: [this._mapView, this._progress],
            title: this.info.displayName
        };
    }

    /****************************************/

    protected createMapAsync(manager: IMapManager): Promise<void> {
        return Promise.resolve();
    }

    /****************************************/

    protected async onMapLoaded() {

        await this.createMapAsync(this._mapView.manager);
        this._progress.visible = false;
    }

    /****************************************/

    abstract info: IViewContentInfo;
}