import { INamedContent } from "../../Core/Abstraction/INamedContent";
import { linq } from "../../Core/Linq";
import { addClass, removeClass } from "../../Core/Utils/DomUtils";
import { BindableObject } from "../../Templating/BindableObject";
import { IAction } from "../Abstraction/IAction";
import { IRefreshable } from "../Abstraction/IRefreshable";
import { IView, ViewStyle } from "../Abstraction/IView";
import { IViewContent } from "../Abstraction/IViewContent";
import { IViewContentHost } from "../Abstraction/IViewContentHost";
import { IViewContentInfo } from "../Abstraction/IViewContentInfo";
import { ActivateContentReason, IViewContentProvider } from "../Abstraction/IViewContentProvider";
import { IListViewConfig, ListView } from "../Components/ListView";
import { NavBar } from "../Components/NavBar";
import { View } from "../Components/View";
import { formatForCss } from "../Helpers/ViewUtils";
import { ItemsSource } from "../Items/ItemsSource";
import { PagedItemsLoader } from "../Items/PagedItemsLoader";

export interface IItemListContentOptions<TItem, TItemView extends View, TFilter> {

    pageSize?: number;

    title?: string;

    footer?: IView[];

    url?: string;

    itemsSource: ItemsSource<TItem, any, TFilter>;

    filters: INamedContent<TFilter>[];

    styles?: ViewStyle[];

    actions?: IAction[];

    listView?: IListViewConfig<TItem, TItemView, TFilter>;

    canSearch?: boolean;

    isBodyScroll?: boolean;
}

/****************************************/

export abstract class ItemListContent<TItem, TItemView extends View, TFilter> extends BindableObject implements IViewContentProvider, IRefreshable {

    protected _listView: ListView<TItem, TItemView>;
    protected _tabView: NavBar<TFilter>;
    protected _isRefreshing = false;
    protected _isBodyScroll = false;

    /****************************************/

    constructor() {
        super();
    }

    /****************************************/

    async refreshAsync() {

        this._isRefreshing = true;
        try {
            if (await this.prepareFilterAsync(this._listView.filter))
                await this._listView.refreshAsync();
        }
        finally {
            this._isRefreshing = false;
        }
    }

    /****************************************/

    protected prepareFilterAsync(filter: TFilter) {
        return Promise.resolve(true);
    }

    /****************************************/

    deactivateAsync() {

        if (this._isBodyScroll)
            this.removeContent();

        this._listView?.deactivateListener();

        return Promise.resolve();
    }

    /****************************************/

    async activateAsync(reason: ActivateContentReason) {

        this._listView?.activateListener();

        if (reason == "loading")
            await this.refreshAsync();
    }

    /****************************************/

    abstract configureAsync(host: IViewContentHost): Promise<IItemListContentOptions<TItem, TItemView, TFilter>>;

    /****************************************/

    async getContentAsync(host: IViewContentHost): Promise<IViewContent> {

        const views: IView[] = [];

        const options = await this.configureAsync(host);

        this._listView = new ListView({
            itemsSource: options.itemsSource,
            itemsLoader: options.pageSize ? new PagedItemsLoader({
                pageSize: options.pageSize
            }) : undefined,
            ...options.listView
        });

        this._listView.loadAsync = () => Promise.resolve();

        if (options.filters) {

            if (options.filters.length > 1) {
                this._tabView = new NavBar({
                    styles: ["tab-view"],
                    itemTemplate: "TextView",
                    itemBehavoirs: ["ripple"],
                    onItemSelected: item => {
                        this._listView.filter = item.content;
                        this.refreshAsync();
                    },
                    items: linq(options.filters).select(a => ({
                        name: a.name,
                        text: a.name,
                        content: a.content
                    })).toArray()
                });

                views.push(this._tabView);
            }
            else if (options.filters.length == 1)
                this._listView.filter = options.filters[0].content;
        }

        views.push(this._listView);

        if (this._tabView)
            this._tabView.selectedItem = this._tabView.content.get(0);

        if (options.footer)
            options.footer.forEach(a => views.push(a));

        this._listView.activateListener();

        //await this.refreshAsync();

        if (options.isBodyScroll) {
            this._isBodyScroll = true;
            setTimeout(() => this.moveContent());
        }

        return Promise.resolve({
            title: options.title ?? this.info.displayName,
            actions: options.actions,
            styles: options.styles,
            views: views,
            url: options.url,
            searchAsync: options.canSearch ? text=> this.searchAsync(text) : undefined
        } as IViewContent);
    }

    /****************************************/

    protected removeContent() {

        removeClass(document.scrollingElement, "scroll");
        removeClass(document.body.parentElement, "body-scroll");
        const element = document.querySelector("." + formatForCss(this.info.name));
        if (element)
            element.remove();
    }

    /****************************************/

    protected moveContent() {

        const element = document.querySelector("." + formatForCss(this.info.name));
        let container = document.querySelector(".scroll-content") as HTMLDivElement;
        if (!container) {
            container = document.createElement("div");
            container.classList.add("scroll-content");
            document.body.insertBefore(container, document.body.firstChild);
        }
        container.innerHTML = "";
        container.appendChild(element);

        addClass(document.body.parentElement, "body-scroll");
        removeClass(element, "scroll");
        addClass(document.scrollingElement, "scroll");
    }


    /****************************************/

    setFilter(name: string, content: TFilter): boolean {

        this._listView.filter = content;

        if (this._tabView) {
            let item = this._tabView.getItem(name);
            if (!item) {
                item = this._tabView.addItem({
                    text: name,
                    name: name,
                    content: content
                })
            }
            else {
                item.content = content;
            }

            const isSelected = this._tabView.selectedItem == item;
            this._tabView.selectedItem = item;

            return isSelected;
        }
        else 
            return true;
    }

    /****************************************/

    async searchAsync(text: string) {

        this._listView.filter = { ...this._listView.filter, ...this._listView.itemsSource.getFilter(text)  };

        await this._listView.refreshAsync();
    }

    /****************************************/

    get listView() {
        return this._listView;
    }

    abstract info: IViewContentInfo;
}