import { Services } from "../Global";
import { LocalString } from "../Types/DynamicString";
import { AppEvent } from "../Types/Event";
import { IOperation, IOperationProgress, OperationType } from "./IOperation";

declare global {

    export interface IServices {

        operation: IOperationManager;
    }
}


export interface IOperationConfig extends IOperationProgress {

    type?: OperationType;

    parentOperation?: IOperation;

    showProgress?: boolean;
}

export interface IOperationManager {

    progress(progress: IOperationProgress | LocalString);

    begin(message: LocalString | IOperationConfig): IOperation;

    end(operation: IOperation);

    readonly onBegin: AppEvent<IOperation>;

    readonly onEnd: AppEvent<IOperation>;

    readonly onProgress: AppEvent<{ operation: IOperation, progress: IOperationProgress }>;

    readonly current: IOperation;

    readonly operations: Iterable<IOperation>;
}


export async function execAsync<T>(action: (operation? : IOperation) => Promise<T>, operation: string | IOperationConfig = "Executing")  {
    const op = Services.operation.begin(operation);
    try {
        return await action(op);
    }
    catch (ex) {
        Services.application.handleError(action, ex);
    }
    finally {
        op.end();
    }
}