import { IAnalytics } from "../Abstraction/IAnalytics";

declare global {

    function gtag(...args: any[]): void;
}

/****************************************/

export class GoogleAnalytics implements IAnalytics {

    constructor(gaId?: string) {
        this.gaId = gaId ?? window["__gaid"];

        if (!("gtag" in window))
            window["gtag"] = () => { };
    }

    /****************************************/

    pageView(uri: string): void {

        gtag("config", this.gaId, { page_path: uri });
    }

    /****************************************/

    action(name: string, data?: string): void {

        gtag("event", name, {
            event_category: 'Action',
            event_label: data
        });
    }

    /****************************************/

    gaId: string;
}