import { linq } from "../../Core/Linq";
import { IAction } from "../Abstraction/IAction";
import { IAppContent } from "../Abstraction/IAppContent";
import { IAppContentManager } from "../Abstraction/IAppContentManager";

export class AppContentManager<TName extends string> implements IAppContentManager<TName> {

    constructor(contents: IAppContent[]) {

        this.contents = contents;
    }

    /****************************************/

    get(name: TName): IAppContent {
        return linq(this.contents).first(a => a.name == name);
    }

    /****************************************/

    actionFor(name: TName): IAction {
        const content = this.get(name);
        return {
            executeAsync: content.openAsync,
            name: content.name,
            displayName: content.displayName,
            icon: content.icon
        };
    }

    /****************************************/

    contents: IAppContent[];
}